import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const Common = styled.div`
   background: #F7F7F7;
   border-radius: 20px;
   width: 200px;
   height: 120px;
`;

const SelectedScenarios = () => {
    return (
        <Common key={1}>
            SelectedScenarios
            избранные сценарии
        </Common>
    );
};

export default SelectedScenarios;



